		}
	}

	.masterplans-detail-description {
		width: calc(100vw - 16px * 2);
		margin: 50px 16px;

		h3 {
			margin-top: 33px;
		}
	}

	.masterplans-detail-slabs {
		&__status {
			height: 20px;
			margin: 44px 16px 10px;
		}

		&__photos {
			display: grid;
			grid-template-columns: 100%;
			padding: 0;
			grid-column-gap: 0;
			grid-row-gap: 2px;
		}

		&__buttons {
			display: none;
		}
	}
}